import { Component } from '@angular/core';

@Component({
    selector: 'card',
    templateUrl: './card.component.html',
    styleUrls: ['./card.component.css']
})

export class CardComponent {
    public isHover: Boolean = false;

    hover() {
        this.isHover = !this.isHover;
    }
}


